@import '../../node_modules/codemirror/lib/codemirror.css';
@import '../../node_modules/codemirror/theme/material-palenight.css';

$dark: #19192c;
$background: #1f1e37;
$font: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu;

.App {
  align-items: center;
  background-color: #6b4bff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  top: 0;
  width: 100vw;
}

.code-screen {
  align-self: center;
  background-color: $dark;
  border-radius: .825rem;
  box-shadow: 0 0 1.25rem .125rem rgba(0, 0, 0, .25);
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  max-height: 80vh;
  max-width: 70vw;
  width: 100%;

  .section {
    overflow: hidden;
    position: relative;
    width: 49%;

    &:first-child {
      border-bottom-left-radius: .625rem;
      border-top-left-radius: .625rem;
    }

    &:last-child {
      border-bottom-right-radius: .625rem;
      border-top-right-radius: .625rem;
    }
    
    &-header {
      align-items: center;
      background-color: $dark;
      border-bottom: 1px solid #51ffe7;
      box-sizing: border-box;
      display: flex;
      height: 4rem;
      justify-content: space-between;
      padding: 1.25rem;
      width: 100%;

      &__column {
        align-items: center;
        display: flex;
      }

      &__status {
        color: transparent;

        &--invalid {
          color: #e51758;
        }

        &--valid {
          color: #51ffe7;
        }
      }

      &__text {
        color: #7070c3;
        font-size: .85rem;
        text-indent: .75rem;
      }
    }

    &--browser {
      align-items: center;
      background-color: #292d3e;
      display: flex;
      flex-direction: column;
    }

    &--code {
      background-color: #292c3e;
    }

    &--console {
      background-color: $background;
      color: #ffffff;
      font-family: 'Courier New', Courier, monospace;

      pre {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        height: 100%;
        margin: 0;
        padding: 1.25rem;
      }
    }

    iframe {
      border: none;
      height: 100%;
      overflow: scroll;
      width: 100%;
    }
  }
}

.browser {
  &-header {
    align-items: center;
    background-color: #1687fb;
    border-radius: .625rem .625rem 0 0;
    box-sizing: border-box;
    color: rgba(255, 255, 255, .75);
    display: flex;
    height: 4rem;
    padding: 1rem;
    justify-content: center;
    margin-top: 1.25rem;
    width: calc(100% - 2.5rem);
  }

  &-address-bar {
    background-color: rgba(0, 0, 0, .5);
    border: none;
    border-radius: .25rem;
    box-sizing: border-box;
    color: #ffffff;
    display: flex;
    flex-grow: 1;
    margin-right: 1.25rem;
    padding: .5rem;
  }

  &-body {
    background-color: #ffffff;
    border-radius: 0 0 .625rem .625rem;
    height: calc(100% - 6.25rem);
    width: calc(100% - 2.5rem);
  }
}

.code-check-button {
  background-color: #13ffbd;
  border: none;
  border-radius: 0.25rem;
  font-family: $font;
  padding: 0.5rem 0.75rem;
  color: #0a4c3a;
  font-weight: 800;
  text-shadow: 0 0 #c2ffee;
  font-size: .825rem;
}

.CodeMirror {
  height: calc(70vh - 4rem);
}

.lesson {
  &-section {
    background-color: $dark;
    // border: 1.25rem solid $dark;
    border-radius: .325rem;
    box-shadow: 0 0 1.5rem 0.125rem rgba(0, 0, 0, .8);
    box-sizing: border-box;
    color: #ffffff;
    font-family: $font;
    margin-bottom: 3rem;
    width: 70vw;

    &__header {
      border-bottom: 1px solid #6b4aff47;
      box-sizing: border-box;
      padding: 1.75rem 1.75rem 2.75rem 1.75rem;
      width: 100%;
    }

    &__content {
      background-color: rgba(161, 141, 255, .09);
      border-radius: .325rem;
      padding: 1rem 1.75rem;
    }

    h1 {
      font-size: 3rem;
      font-weight: 100;
      margin: 0;

      em {
        border-bottom: 2px solid #1687fb;
        font-style: normal;
      }
    }

    h3 {
      font-size: 2rem;
      font-weight: 300;

      em {
        border-bottom: 2px solid #1687fb;
        font-style: normal;
      }
    }

    p {
      color: #cdc2ff;
      font-size: 1.325rem;
      font-weight: 400;
      line-height: 2rem;

      a {
        color: #00e6ff;
      }

      strong {
        color: #a18dff;
      }

      code {
        font-size: 1rem;
      }
    }

    ul {
      color: #cdc2ff;
      font-size: 1.325rem;
      line-height: 3rem;
      list-style: circle;

      code {
        font-size: 1rem;
      }
    }

    code {
      background-color: #292d3e;
      border: 1px solid #a18dff;
      border-radius: 0.325rem;
      color: #a18dff;
      padding: .325rem 0.5rem;
    }
  }

  &-container {
    align-items: center;
    box-sizing: border-box;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    font-family: $font;
    justify-content: center;
    overflow: scroll;
    padding: 4rem;
    width: 80vw;
  }
}